
import { defineComponent, ref, provide } from 'vue'

export default defineComponent({
  name: 'GDropListPopover',
  props: {
    trigger: {
      type: String,
      default: 'hover',
    },
    placement: {
      type: String,
      default: 'bottom',
    },
  },
  setup() {
    const visible = ref(false)

    provide('hidePopover', () => {
      visible.value = false
    })

    return {
      visible,
    }
  },
})
